@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
* {
  margin: 0;
  font-family: 'Comfortaa', cursive; 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.guest{
    border: 5px solid #bfdfe0;
    border-radius: 50%;
    width: 150px;
    background-color: white;
}
.loginContainer{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgb(2,0,36);
    background: linear-gradient(0deg, rgb(75, 194, 168) 19%, rgb(39, 151, 151) 36%, rgb(15, 38, 88) 100%);
}
.formInput{
    border: none;
    background-color: rgba(73, 252, 222, 0);
    padding-top: 15px;
    padding-left: 10px;
    padding-bottom: 15px;
    font-size: 16px;
}
.form{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.459);
    margin-top: 26px;
}
.ico{
    display: inline;
    height: 32px;
    width: 32px;
    padding: 0px 20px;
}
.login{
    font-size: 20px;
    width: 80%;
    margin-top: 26px;
    padding: 14px;
    background-color: rgba(0, 0, 0, 0.705);
    color: white;
    border: 2px solid transparent;
    transition: 0.2s;
}
.login:hover{
    border: 2px solid white;
}
form{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.formError{
    color:rgb(255, 255, 255);
    font-size: 20px;
    margin-top: 14px;
    padding: 10px;
    width: 80%;
    background-color: rgba(255, 0, 0, 0.733);
    text-align: center;
}
.card{
    margin: 4px;
    width: 60%;
    border: 2px solid white;
    padding: 10px;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}
.add{
    background: rgb(0 117 255);
    color: white;
    font-size: 30px;
    height: 28px;
    width: 28px;
    text-align: center;
    display: inline-block;
    align-items: center;
    justify-content: center;
    border-radius: 10%;
    font-weight: bolder;
    cursor: pointer;
}
.add:hover{
    background: rgb(8 69 202);
}
.formTask{
    margin-top: 20px;
    display: flex;
    align-items: center;
    background: cornflowerblue;
    padding: 10px;
    margin-bottom: 20px;
}
.taskC{
    background-color: teal;
}
.taskF{
    background-color: tomato;
}
.markedBox{
    font-size: 30px;
    border: white 1px solid;
    padding: 5px;
    background-color: rgba(240, 255, 240, 0.438);
    margin: 5px;
}
.markedBox:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}
.checkT{
    color: greenyellow;
}
.checkF{
    color: red;
}
.homeContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgb(44, 45, 138);
}
.card .ico{
    border: white 1px solid;
    padding: 5px;
    background-color: rgba(240, 255, 240, 0.438);
    margin-top: 15px;
}
.card .ico:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

input[type=checkbox]{
  /* Double-sized Checkboxes */ /* IE */ /* FF */
  -webkit-transform: scale(2.1); /* Safari and Chrome */ /* Opera */
transform: scale(2.1);
margin: 10px;
}

@media (min-width:650px) {
    .card{
        flex-direction: row;
    }
    .card .ico{
        margin: 0px;
        padding: 4px;
    }
}
